import React from 'react';
import "../pages/Features.css"
import {Grid,Typography,Box} from "@mui/material";
import VaccinesIcon from '@mui/icons-material/Vaccines';
import LoyaltyIcon from '@mui/icons-material/Loyalty';
import PetsIcon from '@mui/icons-material/Pets';
import ChildCareIcon from '@mui/icons-material/ChildCare';
import { useInView } from "react-intersection-observer";

function Features() {
    const [ref,inView]=useInView();
  return (
    <section id="Features" ref={ref}>
    <Grid container spacing={2}>
       <Grid item xs={12} sm={12} md={6}>
            <Box>
                <Typography variant="h6" component="h2">Our Features</Typography>
            </Box>
            <Box sx={{marginTop:5}}>
                <Typography sx={{fontFamily:"syne",fontWeight:"600",color:"#34495e"}} variant="h4" component="h2">Our Expert Staff Ensures Optimal Cat Care!</Typography>
            </Box>
            <Box>
                <Typography sx={{fontFamily:"syne",color:"#34495e",textAlign:"justify",marginTop:2}} variant="body" component="p">At our facility, we take pride in providing exceptional care for your beloved cats. Our highly trained staff is dedicated to ensuring their well-being and happiness, giving you peace of mind while you're away.
                </Typography>
            </Box>


                <Box>
                    <Box sx={{display:"flex",marginTop:5,transition:"all 2s ease-out",transform:`translateX(${inView?"0px":"-200px"})`}}>

                        <VaccinesIcon sx={{fontSize:"2rem" , backgroundColor:"#f27676",color:"white", padding:2,borderRadius:"25px"}}/>
                        <Box sx={{marginLeft:3}}>
                        <Typography variant="h6" compnent="h3">Vaccinations</Typography>
                        <Typography sx={{textAlign:"justify",fontSize:"0.9rem"}}>
                        By staying up-to-date with vaccinations, you provide your feline friend with the best protection and ensure a long and healthy life.
                        </Typography>
                        </Box>
                        
                    </Box>
                    <hr/>
                    <Box sx={{display:"flex",marginTop:2 ,transition:"all 2.5s ease-out",transform:`translateX(${inView?"0px":"-200px"})`}}>

                        <LoyaltyIcon sx={{fontSize:"2rem" , backgroundColor:"#f27676",color:"white", padding:2,borderRadius:"25px"}}/>
                            <Box sx={{marginLeft:3}}>
                                    <Typography variant="h6" compnent="h3">
                                        Nutrition Counselling 
                                    </Typography>
                                    <Typography sx={{textAlign:"justify",fontSize:"0.9rem"}}>
                                    Our cat nutrition counseling services are tailored to ensure your feline companion receives the best dietary care for their specific needs. Our knowledgeable experts offer personalized guidance, helping you make informed choices to promote your cat's overall health, vitality, and well-being.
                                    </Typography>
                            </Box>

                    </Box>


                    <hr/>

                    <Box sx={{display:"flex",marginTop:2,transition:"all 3s ease-out",transform:`translateX(${inView?"0px":"-200px"})`}}>

                        <PetsIcon  sx={{fontSize:"2rem" , backgroundColor:"#f27676",color:"white", padding:2,borderRadius:"25px"}}/>
                             <Box sx={{marginLeft:3}}>
                                     <Typography variant="h6" compnent="h3">
                                     Cat Insurance 
                                     </Typography>
                                      <Typography sx={{textAlign:"justify",fontSize:"0.9rem"}}>
                                      Cat insurance offers comprehensive coverage for veterinary expenses, ensuring that your beloved feline companion receives the necessary medical care without the burden of financial strain.
                                        </Typography>
                             </Box>

                    </Box>
                    <hr/>

                    <Box sx={{display:"flex",marginTop:2}}>

                        <ChildCareIcon  sx={{fontSize:"2rem" , backgroundColor:"#f27676",color:"white", padding:2,borderRadius:"25px"}}/>
                            <Box sx={{marginLeft:3}}>
                                    <Typography variant="h6" compnent="h3">
                                    Dental care
                                    </Typography>
                                    <Typography sx={{textAlign:"justify",fontSize:"0.9rem"}}>
                                    Maintaining your cat's dental health is crucial. Regular check-ups and a consistent dental care routine help prevent dental issues and promote overall well-being.
                                        </Typography>
                            </Box>

                        </Box>
                </Box>


       </Grid> 

       <Grid item xs={12} sm={12} md={6}>
          <Box sx={{margin:"auto",textAlign:"center", padding:3,marginTop:10}}>
          <img src="image/13.png" alt=""/>
          </Box>
       </Grid> 
    </Grid>

    </section>




  )
}

export default Features;