import React from 'react';
import "../pages/Footer.css"
import {Box, Grid ,Typography,Button} from "@mui/material";
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import MailIcon from '@mui/icons-material/Mail';
import LocationOnIcon from '@mui/icons-material/LocationOn';



function Footer() {

    const pages = ['Home', 'About', 'Services','Team'];



  return (
   <section id="Footer">
   <Grid container spacing ={5}>
             <Grid item xs={12} sm={12} md={4}>
             <img src="image/logo.png" style={{width:"150px", height:"auto"}} alt=""/>
             <Typography variant='body' component="div" sx={{color:"white", textAlign:"justify",marginTop:2}}> 
                          "Welcome to Cattitude, your trusted destination for comprehensive cat care and veterinary services. With our passionate team of experts, we provide exceptional care, ensuring your feline companions lead happy, healthy lives."
             </Typography>
             <Box sx={{marginTop:4}}>
             <Button sx={{color:"white" ,padding:1,backgroundColor:"#2497c3",borderRadius:25}}>
                <FacebookIcon/>
             </Button>

             <Button sx={{color:"white" ,padding:1,backgroundColor:"#2497c3",borderRadius:25,marginLeft:2}}>
                <InstagramIcon/>
             </Button>
             <Button sx={{color:"white" ,padding:1,backgroundColor:"#2497c3",borderRadius:25,marginLeft:2}}>
                <TwitterIcon/>
             </Button>
             

             </Box>

            </Grid>


<Grid item xs={12} sm={12} md={4}>
<Typography variant='h5' component="div" sx={{color:"white",margin:"auto",textAlign:"center"}}> Quick Link</Typography>
<hr style={{width:"30%",marginBottom:5}}/>
<Box sx={{marginTop:2}}>
{pages.map((page) => {
  return <Box sx={{color:"white",margin:"auto",textAlign:"center"}}> <Button sx={{color:"white"}} href={`#${page}`}> {page} </Button> </Box>

})}
</Box>
    
</Grid>

            <Grid item xs={12} sm={12} md={4}>
            <Typography variant='h6' component="div" sx={{color:"white"}}> Contact us</Typography>
            <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
                     <PhoneInTalkIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
                     <Typography variant='subtitle2' component="p" sx={{marginLeft:2,color:"white"}}>+15 00x xxxx</Typography>
             </Box>


             <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
                    <MailIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
                     <Typography variant='subtitle2' component="p" sx={{marginLeft:2,color:"white"}}>info@catitude.com</Typography>
            </Box>



             <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
                     <LocationOnIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
                     <Typography variant='subtitle2' component="p" sx={{marginLeft:2,color:"white"}}>TX,USA</Typography>
             </Box>
            </Grid>

   </Grid>

 

   </section>
   
  )
}

export default Footer