import * as React from 'react';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import Toolbar from '@mui/material/Toolbar';
import IconButton from '@mui/material/IconButton';
import Menu from '@mui/material/Menu';
import MenuIcon from '@mui/icons-material/Menu';
import Container from '@mui/material/Container';
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import MenuItem from '@mui/material/MenuItem';
import './Appbar.css';
import {HashLink} from "react-router-hash-link"

const pages = ['Home', 'About', 'Services','Team'];


function ResponsiveAppBar() {
  const [anchorElNav, setAnchorElNav] = React.useState(null);


  const handleOpenNavMenu = (event) => {
    setAnchorElNav(event.currentTarget);
  };
//   const handleOpenUserMenu = (event) => {
//     setAnchorElUser(event.currentTarget);
//   };

  const handleCloseNavMenu = () => {
    setAnchorElNav(null);
  };

//   const handleCloseUserMenu = () => {
//     setAnchorElUser(null);
//   };

  return (
    <AppBar className="appbar" position="fixed" sx={{backgroundColor:"white", color:"black"}}>
      <Container maxWidth="xl">
        <Toolbar disableGutters>
        
       
          <Box sx={{ flexGrow: 1, display: { xs: 'flex', md: 'none' } }}>
         
            <IconButton
              size="large"
              aria-label="account of current user"
              aria-controls="menu-appbar"
              aria-haspopup="true"
              onClick={handleOpenNavMenu}
              color="inherit"
            >
              <MenuIcon />
              <img  src='image/logo.png' alt='logo ' style={{width:"auto", maxHeight:"50px", display: { xs: 'none', md: 'flex' }}}/>
            </IconButton>
            <Menu
              id="menu-appbar"
              anchorEl={anchorElNav}
              anchorOrigin={{
                vertical: 'bottom',
                horizontal: 'left',
              }}
              keepMounted
              transformOrigin={{
                vertical: 'top',
                horizontal: 'left',
              }}
              open={Boolean(anchorElNav)}
              onClose={handleCloseNavMenu}
              sx={{
                display: { xs: 'block', md: 'none' },
              }}
            >
              {pages.map((page) => (
                <MenuItem className="collapseNav" key={page} onClick={handleCloseNavMenu} sx={{textAlign:"center",margin:"auto",color:"black"}}>
                {/* <Link  to={`/#${page}`}> {page}</Link> */}
                <HashLink smooth to={`#${page}`}> {page} </HashLink>
                </MenuItem>
              ))}
            </Menu>
          </Box>
          
        
          <Box sx={{ flexGrow: 1, display: { xs: 'none', md: 'flex' } ,textAlign:"center"}}>
          <img  src='image/logo.png' alt='logo ' style={{width:"auto", maxHeight:"60px", display: { xs: 'none', md: 'flex' }}}/>
           <Box sx={{margin:"auto", display:"flex"}}>
            {pages.map((page) => (
              <Button
                key={page}
                onClick={handleCloseNavMenu}
                sx={{ my: 2, color: 'black', display: 'block' ,textAlign:"center",marginLeft:3 }}
              >
                {/* <Link sx={{color:"black"}} to={`/#${page}`}> {page}</Link> */}
                <HashLink smooth to={`#${page}`}> {page} </HashLink>
              </Button>
            ))}
            </Box>
          </Box>

          <Box sx={{ flexGrow: 0 }}>
            <Tooltip title="contact-us">
          
              <Button sx={{backgroundColor:"#ffd6ef"}} href="#Contact" variant="contained">Contact-us</Button>
           
            </Tooltip>
           
          </Box>
        </Toolbar>
      </Container>
    </AppBar>
  );
}
export default ResponsiveAppBar;