import React from 'react';
import "../pages/Team.css";
import {Box,Typography,Grid,Card,CardActions,CardContent,CardMedia,Button} from "@mui/material";
import FacebookIcon from '@mui/icons-material/Facebook';
import InstagramIcon from '@mui/icons-material/Instagram';
import TwitterIcon from '@mui/icons-material/Twitter';
import { useInView } from 'react-intersection-observer';

function Team() {
      const [ref,inView]=useInView();
  return (
   <section id="Team" ref={ref}>
   <Box>
    <Typography variant='h6' component="h2" sx={{textAlign:"center",marginTop:3}}>Our Team</Typography>
    <Typography variant='h4' component="h2" sx={{textAlign:"center",marginTop:3,fontFamily:"syne",fontWeight:"800"}}>Discover our Team</Typography>
   </Box>
  <Box>
    <Grid container spacing={2} sx={{marginTop:5}}>
        <Grid item xs={12} sm={12} md={3} sx={{transition:"all 1s ease-out",transform:`translateY(${inView?"0px":"200px"})`}}>
        <Card sx={{ maxWidth: 345 }}>
            <CardMedia  component="img"
        alt="green iguana"
        height="340"
        image="image/vets3.jpg"></CardMedia>

        <CardContent >
             <Typography gutterBottom variant="h5" component="div" sx={{textAlign:"center"}}>
                  Velma S Berry
             </Typography>
             <Typography variant="body2" color="text.secondary" sx={{textAlign:"center"}}>
                  veterinary
             </Typography>
      </CardContent>

      <CardActions>
      <Box sx={{display:"flex" , textAlign:"center",margin:"auto",justifyContent:"space-around"}}>
      <Button href="https://facebook.com" sx={{color:"black"}}>
            <FacebookIcon />
      </Button>
      <Button href="https://twitter.com" sx={{color:"black"}}>
            <TwitterIcon />
      </Button>
      <Button href="https://instagram.com" sx={{color:"black"}}>
            <InstagramIcon />
      </Button>
       
      </Box>

      </CardActions>
        </Card>
        </Grid>




        <Grid item xs={12} sm={12} md={3} sx={{transition:"all 2s ease-out",transform:`translateY(${inView?"0px":"200px"})`}}>
        <Card sx={{ maxWidth: 345 }}>
            <CardMedia  component="img"
        alt="green iguana"
        height="340"
        image="image/vets4.jpg"></CardMedia>

        <CardContent sx={{backgroundColor:"#f27676",color:"white"}}>
             <Typography gutterBottom variant="h5" component="div" sx={{textAlign:"center"}}>
                 J. Harry
             </Typography>
             <Typography variant="body2" color="text.secondary" sx={{textAlign:"center",color:"white"}}>
                  Groomer Manager
             </Typography>
      </CardContent>

      <CardActions sx={{backgroundColor:"#f27676"}}>
      <Box sx={{display:"flex" , textAlign:"center",margin:"auto",justifyContent:"space-around"}}>
      <Button href="https://facebook.com" sx={{color:"white"}}>
            <FacebookIcon />
      </Button>
      <Button href="https://twitter.com" sx={{color:"white"}}>
            <TwitterIcon />
      </Button>
      <Button href="https://instagram.com" sx={{color:"white"}}>
            <InstagramIcon />
      </Button>
       
      </Box>

      </CardActions>
        </Card>
        </Grid>







        <Grid item xs={12} sm={12} md={3} sx={{transition:"all 3s ease-out",transform:`translateY(${inView?"0px":"200px"})`}}>
        <Card sx={{ maxWidth: 345 }}>
            <CardMedia  component="img"
        alt="green iguana"
        height="340"
        image="image/vets5.jpg"></CardMedia>

        <CardContent >
             <Typography gutterBottom variant="h5" component="div" sx={{textAlign:"center"}}>
                 Michael Jhonson
             </Typography>
             <Typography variant="body2" color="text.secondary" sx={{textAlign:"center"}}>
                Day-Care Manager
             </Typography>
      </CardContent>

      <CardActions>
      <Box sx={{display:"flex" , textAlign:"center",margin:"auto",justifyContent:"space-around"}}>
      <Button href="https://facebook.com" sx={{color:"black"}}>
            <FacebookIcon />
      </Button>
      <Button href="https://twitter.com" sx={{color:"black"}}>
            <TwitterIcon />
      </Button>
      <Button href="https://instagram.com" sx={{color:"black"}}>
            <InstagramIcon />
      </Button>
       
      </Box>

      </CardActions>
        </Card>
        </Grid>




        <Grid item xs={12} sm={12} md={3} sx={{transition:"all 4s ease-out",transform:`translateY(${inView?"0px":"200px"})`}}>
        <Card sx={{ maxWidth: 345 }}>
            <CardMedia  component="img"
        alt="green iguana"
        height="340"
        image="image/vets6.jpg"></CardMedia>

        <CardContent >
             <Typography gutterBottom variant="h5" component="div" sx={{textAlign:"center"}}>
                C. Gomez
             </Typography>
             <Typography variant="body2" color="text.secondary" sx={{textAlign:"center"}}>
                  Boarding Manager
             </Typography>
      </CardContent>

      <CardActions>
      <Box sx={{display:"flex" , textAlign:"center",margin:"auto",justifyContent:"space-around"}}>
      <Button href="https://facebook.com" sx={{color:"black"}}>
            <FacebookIcon />
      </Button>
      <Button href="https://twitter.com" sx={{color:"black"}}>
            <TwitterIcon />
      </Button>
      <Button href="https://instagram.com" sx={{color:"black"}}>
            <InstagramIcon />
      </Button>
       
      </Box>

      </CardActions>
        </Card>
        </Grid>
    </Grid>
  </Box>

   </section>
  )
}

export default Team;