import React from 'react'
import {Grid,Typography} from "@mui/material"

function SecFooter() {
    const date= new Date().getFullYear();
  return (
    <div>

            <Grid container sx={{backgroundColor:"#154b83",padding:2}}>
                <Grid item xs={12}>
                    <Typography variant='body' component="div" sx={{color:"#fff",textAlign:"center"}}> All reserve for Md Soliman - {date}</Typography>

                </Grid>
            </Grid>



    </div>
  )
}

export default SecFooter