import React from 'react';
import "./Contact.css";
import {Grid,Box,Typography,TableCell,TableRow,TableContainer,FormControl,Button} from "@mui/material"
import PhoneInTalkIcon from '@mui/icons-material/PhoneInTalk';
import MailIcon from '@mui/icons-material/Mail';
import LocationOnIcon from '@mui/icons-material/LocationOn';
import SettingsEthernetIcon from '@mui/icons-material/SettingsEthernet';
import TextField from '@mui/material/TextField';

function Contact() {
  return (
    <section id="Contact">
    
    <Grid container spacing={5} >
    <Grid item xs={12} sm={12} md={6} >
    <Typography variant='h4' component="h2" sx={{fontFamily:"syne",fontWeight:"800",marginBottom:5}}>
      Contact us
    </Typography>
    <Typography variant='body' component="p" sx={{marginBottom:5}}> Contact information for cat care and veterinary services can vary depending on the specific provide.</Typography>

    <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
        <PhoneInTalkIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
        <Typography variant='subtitle2' component="p" sx={{marginLeft:2}}>+15 00x xxxx</Typography>
    </Box>


    <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
        <MailIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
        <Typography variant='subtitle2' component="p" sx={{marginLeft:2}}>info@catitude.com</Typography>
    </Box>



    <Box sx={{display:"flex" ,marginTop:2, alignItems:"center"}}>
        <LocationOnIcon sx={{backgroundColor:"#f27676",color:"#f2f2f2" ,padding:1,borderRadius:25}}/>
        <Typography variant='subtitle2' component="p" sx={{marginLeft:2}}>TX,USA</Typography>
    </Box>
    
    <Box>
      <Typography variant='h4' component="h2" sx={{fontFamily:"syne",fontWeight:"800",marginTop:2}}> Working Hours:</Typography>
      <hr/>
    </Box>
    <br/>
    <Box>
    <br/>
      <TableContainer component="paper"  sx={{ width:"100%" }}>
      <TableRow>
        <TableCell> 
            <Typography> Sunday </Typography>
        </TableCell>
        <TableCell> 
            <Typography> <SettingsEthernetIcon/> </Typography>
        </TableCell>
        <TableCell> 
            <Typography> 8:00 AM- 9:00 PM </Typography>
        </TableCell>
      </TableRow>

      <TableRow>
        <TableCell> 
            <Typography> Monday </Typography>
        </TableCell>
        <TableCell> 
            <Typography> <SettingsEthernetIcon/> </Typography>
        </TableCell>
        <TableCell> 
            <Typography> 12:00 AM- 11:00 PM </Typography>
        </TableCell>
      </TableRow>


      <TableRow>
        <TableCell> 
            <Typography> Tuesday </Typography>
        </TableCell>
        <TableCell> 
            <Typography> <SettingsEthernetIcon/> </Typography>
        </TableCell>
        <TableCell> 
            <Typography> 10:00 AM- 10:30 PM </Typography>
        </TableCell>
      </TableRow>



      <TableRow>
        <TableCell> 
            <Typography> Wednessday </Typography>
        </TableCell>
        <TableCell> 
            <Typography> <SettingsEthernetIcon/> </Typography>
        </TableCell>
        <TableCell> 
            <Typography> 1:00 AM- 8:00 PM </Typography>
        </TableCell>
      </TableRow>

      </TableContainer> 
    </Box>

    </Grid>

    <Grid item xs={12} sm={12} md={6} sx={{margin:"auto"}}>
        <Box sx={{backgroundColor:"#fff",padding:5,marginTop:10}}>
          <FormControl action="mailto:seo.mdsoliman@gmail.com" method="POST">
          <Typography variant="h4" component="body" sx={{textAlign:"center", fontFamily:"syne",fontWeight:"800",marginBottom:4}}>Get a service now</Typography>
          <TextField   required
          id="outlined-required"
          label="Name"
       />

          <TextField   required
          id="outlined-required"
          label="Email"
          type="email"
          sx={{marginTop:3}}/>

<TextField
          id="outlined-multiline-flexible"
          label="Message"
          multiline
          sx={{marginTop:3}}
          minRows={4}
        />
<Button variant="contained" sx={{marginTop:4,backgroundColor:"#f27676"}} type="submit">Send</Button>
          </FormControl>
        </Box>
      
    </Grid>

    </Grid>
  
     
    
    
    
    </section>
  )
}

export default Contact;