import React from 'react'
import "../pages/Services.css"
import {Grid,Box,Button} from "@mui/material";
import Typography from '@mui/material/Typography';
import PetsIcon from '@mui/icons-material/Pets';
import Card from '@mui/material/Card';
import CardActions from '@mui/material/CardActions';
import CardContent from '@mui/material/CardContent';
import ContentCutIcon from '@mui/icons-material/ContentCut';
import OtherHousesIcon from '@mui/icons-material/OtherHouses';
import VolunteerActivismIcon from '@mui/icons-material/VolunteerActivism';
import BloodtypeIcon from '@mui/icons-material/Bloodtype';
import {useInView} from "react-intersection-observer"

function Services() {

const [ref,inView]=useInView();  

  


  return (
    <section id="Services" ref={ref}  >
    <Box ref={ref} sx={{marginTop:5,display:"flex",textAlign:"center",alignItems:"center", justifyContent:"center",color:"#f27676"}}>
         <PetsIcon sx={{marginRight:2}}/>
         <Typography variant='h5' component="h2"> Our Services</Typography>
    </Box>
    <Box marginTop={3}>
        <Typography variant='h2' component="h1"  sx={{fontFamily:"syne",fontWeight:600, color: "#261e45",textAlign:"center"}} >
        What we do ?
        </Typography>
    </Box>
   
    <Grid container spacing={2.5} marginTop={5}>
         <Grid item xs={12} sm={12} md={3} sx={{marginTop:4}}>



             <Card   sx={{transition:"all 2s ease-out",transform:`translateX(${inView ? "0px":"-400px"})`, maxWidth: 345 ,":hover":{  boxShadow: 20,transform:"rotate(2deg)"}}}>
             <CardContent>
             <ContentCutIcon sx={{color:"#f27676",fontSize:50}}/>
                 <Typography  gutterBottom variant="h5" component="div" sx={{fontFamily:"syne", fontWeight:600}}>
                 Grooming 
                 </Typography>
                 <Typography variant="body2" color="text.secondary" sx={{textAlign:"justify"}}>
                       Lizards are a widespread group of squamate reptiles, with over 6,000
                        species, ranging across all continents except Antarctica
                 </Typography>
        </CardContent>
        <CardActions >
        <Button size="small"sx={{textAlign:"right"}}>Learn More</Button>
        </CardActions>

             </Card>
             
         </Grid>

         <Grid item xs={12} sm={12} md={3} sx={{marginTop:4}}>



<Card sx={{transition:"all 2s ease-out",transform:`translateY(${inView ? "0px":"200px"})`, maxWidth: 345,backgroundColor:"#f27676",color:"white",":hover":{  boxShadow: 20,transform:"rotate(-2deg)"} }}>
<CardContent>
<OtherHousesIcon  sx={{color:"#ffdcd7",fontSize:50}}/>
    <Typography gutterBottom variant="h5" component="div" sx={{fontFamily:"syne", fontWeight:600}}>
    Boarding 
    </Typography>
    <Typography variant="body2" color="white" sx={{textAlign:"justify"}}>
    Our boarding service provides a safe and comfortable environment for your pets, ensuring they receive proper care, attention, and a home away from home experience.
    </Typography>
</CardContent>
<CardActions >
<Button size="small"sx={{textAlign:"right"}}>Learn More</Button>
</CardActions>

</Card>

</Grid>






<Grid item xs={12} sm={12} md={3} sx={{marginTop:4}}>



             <Card sx={{transition:"all 2s ease-out",transform:`translateY(${inView ? "0px":"-200px"})`, maxWidth: 345 ,":hover":{  boxShadow: 20,transform:"translateX(2px)"}}}>
             <CardContent>
             <VolunteerActivismIcon sx={{color:"#f27676",fontSize:50}}/>
                 <Typography gutterBottom variant="h5" component="div" sx={{fontFamily:"syne", fontWeight:600}}>
                 Day Care 
                 </Typography>
                 <Typography variant="body2" color="text.secondary" sx={{textAlign:"justify"}}>
                 Our day care service offers a stimulating and supervised environment for your pets, providing them with a fun-filled day of activities, socialization, and attentive care while you're busy or at work.
                 </Typography>
        </CardContent>
        <CardActions >
        <Button size="small"sx={{textAlign:"right"}}>Learn More</Button>
        </CardActions>

             </Card>
             
         </Grid>




         <Grid item xs={12} sm={12} md={3} sx={{marginTop:4}}>



             <Card sx={{ transition:"all 2s ease-out",transform:`translateX(${inView ? "0px":"400px"})`,maxWidth: 345 ,":hover":{  boxShadow: 20,transform:"translateX(-2px)"}}}>
             <CardContent>
             <BloodtypeIcon sx={{color:"#f27676",fontSize:50}}/>
                 <Typography gutterBottom variant="h5" component="div" sx={{fontFamily:"syne", fontWeight:600}}>
             Veterian
                 </Typography>
                 <Typography variant="body2" color="text.secondary" sx={{textAlign:"justify"}}>
                       Lizards are a widespread group of squamate reptiles, with over 6,000
                        species, ranging across all continents except Antarctica
                 </Typography>
        </CardContent>
        <CardActions >
        <Button size="small"sx={{textAlign:"right"}}>Learn More</Button>
        </CardActions>

             </Card>
             
         </Grid>





    </Grid>










    

    </section>
  )
}

export default Services;