import React from 'react'
import "../pages/Home.css"
import {Grid,Box,Typography,Button} from "@mui/material";
import Avatar from '@mui/material/Avatar';
import {motion} from "framer-motion";

function Home() {
  
  return (
    <motion.div
    initial={{opacity:0,x:-300,y:-300}}
    animate={{opacity:1,x:0,y:0}}
    exit={{opacity:0,x:-300,y:-300}}
    transition={{duration:0.5}}
    >
    <section id="Home">
    <Box sx={{width:"100%"}}>
        <Grid container spacing={0.5}>
        <Grid item xs={12} sm={6}>
        <Box className="header " sx={{marginTop:11}}>
        <motion.div initial={{opacity:0,x:-100}} animate={{opacity:1,x:0}} exit={{opacity:0,x:-100}}>
            <Typography  variant='h2' component="h1" >
              There is nothing more important to us than your baby 
            </Typography>
            </motion.div>
            <Typography variant='p' component="p" sx={{textAlign:"justify",color:"#261e45"}}>
            Cats are fascinating creatures, known for their independent and mysterious nature. With their graceful movements and captivating eyes, they effortlessly capture our hearts. Whether they're curling up for a nap or engaging in playful antics, cats bring joy and companionship to our lives.
            </Typography>
            <Box className="btnApp">
            <motion.div initial={{opacity:0,x:-100,rotate:45}} animate={{opacity:1,x:0,rotate:0}} exit={{opacity:0,x:-100,rotate:45}}>
            <Button href="" className="btn-app" sx={{marginTop:5}} variant="contained">Make an apointment </Button>
            </motion.div>
            </Box>
            <Box sx={{display:"flex",alignItems:"center",flexDirection:"row",marginTop:3,justifyContent:"space-around" ,width:"40%" }}>
                
                <Avatar alt="Remy Sharp" src="image/vets14.jpg" />
            <Box>
                <Typography variant='h6' component="h2">
                       10k +
                </Typography>
                <Typography variant='subtitle1' component="p">
                      satisfied clients
                </Typography>

                </Box>
               
            </Box>
            
          
        </Box>
          
        </Grid>
        <Grid item xs={12} sm={6}>
      <Box className="headerImage" sx={{padding:8.5}}>
        <img src="image/Meo.png" alt=""/>
      </Box>
         
        </Grid>

        </Grid>
    </Box>

    </section>
    </motion.div>
  )
}

export default Home;