import React  from 'react';
import {Grid,Box,Typography,Button} from "@mui/material"
import "../pages/About.css"
import GroupsIcon from '@mui/icons-material/Groups';
import ThumbUpIcon from '@mui/icons-material/ThumbUp';
import Brightness5Icon from '@mui/icons-material/Brightness5';
import BrowseGalleryIcon from '@mui/icons-material/BrowseGallery';
import LocalDiningIcon from '@mui/icons-material/LocalDining';
import { useInView } from 'react-intersection-observer';

function AboutUs() {


const [ref,inView]=useInView();




  return (


 
   <section id="About"   ref={ref} style={{transition:"all 2s ease-out",opacity:`${inView ? 1:0}`,transform:`translateY(${inView?"0px":"200px"})`}}>
   <Box sx={{marginTop:5}}>
   <Grid container spacing={0.5} sx={{marginTop:5}}>
<Grid item xs={12} sm={6} md={6}>
<img src='image/2.png' alt=""/>
   
  </Grid>
  <Grid item xs={12} sm={6} md={6}>
  <Box>
         <Box sx={{display:"flex",color:"#f27676",marginBottom:5}}>

            <GroupsIcon sx={{fontSize:"2.3rem",marginRight:2,color:"#f27676"}}/>
                  <Typography variant='h5' component="h2">
                    About us
                 </Typography>
        </Box>
        <Box>
            <Typography variant='h4' component="h2" sx={{marginBottom:3}}>
            Bringing Joy to the Care of Your Beloved Cat

            </Typography>
            <Typography variant='subtitle1' component="p" sx={{marginBottom:3 , textAlign:"justify"}}>
            At our dedicated cat care center, we take immense pleasure in providing exceptional care for your cherished feline companion. With our expert team and personalized approach, we ensure your cat receives the utmost attention, love, and comfort they deserve.
            </Typography>
        </Box>
        <Box sx={{display:"flex"}}>
                    <Box sx={{display:"flex" , marginRight:5}}>
                    <ThumbUpIcon sx={{marginRight:2}}/>
                        <Typography variant='h6' component="p">Best Vaterinarian's</Typography>
                    </Box>
                    <Box sx={{display:"flex"}}>
                    <Brightness5Icon sx={{marginRight:2}}/>
                        <Typography variant='h6' component="p">Grooming </Typography>
                    </Box>

        </Box>

        <Box sx={{display:"flex",marginTop:5}}>
                    <Box sx={{display:"flex" , marginRight:7}}>
                    <BrowseGalleryIcon sx={{marginRight:2}}/>
                        <Typography variant='h6' component="p">24/7 Hours care </Typography>
                    </Box>
                    <Box sx={{display:"flex"}}>
                    <LocalDiningIcon sx={{marginRight:2}}/>
                        <Typography variant='h6' component="p">Best food  </Typography>
                    </Box>

        </Box>
        <Box sx={{marginTop:5}}>
            <Button variant='contained' sx={{backgroundColor:"black"}}>More About Us</Button>
        </Box>



  </Box>
  
   
  </Grid>


</Grid>

   </Box>

   </section>

  
  )
}

export default AboutUs;