import React from 'react'
import "../pages/ProjectCounter.css"
import {Typography, Grid} from "@mui/material";
import CountUp from 'react-countup';
import { useInView } from "react-intersection-observer";

function ProjectCount() {
const [ref,inView]=useInView();

  return (
    <section id="counter" ref={ref} >
    <Grid container spacing={2}>
        <Grid item xs={12} sm={12} md={3}>
             
                <Typography variant='h3' component="h2" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center"}}>
              {/* {counter} */}
             
             
              <CountUp end={inView?5:0} duration={10}/> K+
              
            
                     
              
                
                </Typography>
              



                <Typography variant='body' component="p" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center",marginTop:2}}>Happy customers</Typography>
        </Grid>

        <Grid item xs={12} sm={12} md={3}>
                <Typography variant='h3' component="h2" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center"}}><CountUp end={inView?45:0}/>+</Typography>
                <Typography variant='body' component="p" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center",marginTop:2}}>Awards</Typography>
        </Grid>

        <Grid item xs={12} sm={12} md={3}>
                <Typography variant='h3' component="h2" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center"}}><CountUp end={inView?200:0}/>+</Typography>
                <Typography variant='body' component="p" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center",marginTop:2}}>Professionals</Typography>
        </Grid>


        <Grid item xs={12} sm={12} md={3}>
                <Typography variant='h3' component="h2" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center"}}><CountUp end={inView?10:0}></CountUp>+</Typography>
                <Typography variant='body' component="p" sx={{fontFamily:"syne",fontWeight:"600",textAlign:"center",marginTop:2}}>Years of experience</Typography>
        </Grid>



    </Grid>

    </section>
  )
}

export default ProjectCount